import * as Main from '../../Js/main'
export default {
	name: 'Feed',

    components: {MainLayout,LearningTopSection,ContentWrapper,SwiperRow,},
	methods: {
	
		getFormations: function() { 		
	Request.getLocalTable('formations')

	.then((res) => {

		this.formations = res.data.data

	})

}
,
		getWebinars: function() { 		
	Request.getLocalTable('webinars')

	.then((res) => {

		this.webinars = res.data.data

	})

}
,
		getArticle: function() { 		
	Request.getLocalTable('articles')

	.then((res) => {

		this.articles = res.data.data

	})

}
,
		getEvent: function() { 		
	Request.getLocalTable('events')

	.then((res) => {

		this.events = res.data.data

	})

}
,
	},
	mounted(){
			
	this.getFormations()

	this.getWebinars()

	this.getArticle()

	this.getEvent()

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
	},
